/*Elementos generales*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-family: "Share Tech Mono", sans-serif;
  text-shadow: 0 0 8px rgba(255, 0, 0, 0.2), -2px -2px 6px rgba(0, 255, 0, 0.2), 2px 2px 4px rgba(0, 255, 255, 0.2);
  color: rgb(130, 187, 135);
  font-size: clamp(1rem, 0.8rem + 0.8vi, 1.2rem);
  background-image: url(../img/portadabackground.png);
  background-size: cover;
  min-height: 100%;
}
body {
  padding: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
a:hover {
  color: white;
}
.inicio {
  width: clamp(12.5rem, 5rem + 25vi, 25rem);
  height: clamp(6.25rem, 2.5rem + 12.5vi, 12.5rem);
  font-size: clamp(1rem, 0.7rem + 1vi, 1.5rem);
  border: solid 5px rgb(130, 187, 135);
  box-shadow: 0 0 8px rgba(255, 0, 0, 0.5), -2px -2px 6px rgba(0, 255, 0, 0.5), 2px 2px 4px rgba(0, 255, 255, 0.5);
  text-transform: uppercase;
  z-index: 1000;
  position: absolute;
}
.content-cover {
  background-color: rgba(2, 5, 7, 0.8);
  width: 100vw;
  height: 100vh;
  text-align: center;
  top: clamp(0rem, -3.75rem + 12.5vi, 6.25rem);
  position: absolute;
  right: clamp(0rem, -4.8rem + 16vi, 8rem);
  overflow-y: hidden;
  overflow-x: hidden;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-scroll-gen ul {
  background-color: rgb(2, 5, 7);
  list-style: none;
  background-position: right;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.nav-scroll-gen li {
  border: 1px black solid;
  padding: 0px;
}
.nav-scroll-gen a {
  text-decoration: none;
  color: rgb(130, 187, 135);
  text-transform: uppercase;
  margin: 0px;
  width: 100%;
  height: 100%;
  line-height: 5vh;
  display: block;
  text-align: center;
}
.nav-scroll-gen a:hover {
  background-color: white;
  color: black;
}
.text-cover {
  scrollbar-color: black rgb(130, 187, 135);
  overflow-x: hidden;
  height: 95vh;
  padding-left: 5%;
  padding-right: 5%;
  text-align: justify;
  line-height: 2rem;
}
.header h1 {
  text-align: center;
  font-size: clamp(1.5rem, 0.6rem + 3vi, 3rem);
  padding: 5% 5% 2.5% 5%;
}
.header h2 {
  text-align: center;
  font-size: clamp(1rem, 0.7rem + 1vi, 1.5rem);
  padding: 2.5% 5% 5% 5%;
}
h1 a {
  text-decoration: none;
  color: white;
  letter-spacing: 1vw;
}
/*Categoria*/
.container-gen {
  padding-left: clamp(0rem, -7.5rem + 25vi, 12.5rem);
  padding-right: clamp(0rem, -7.5rem + 25vi, 12.5rem);
}
.header-gen {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgba(2, 5, 7, 0.8);
  text-align: center;
  overflow: hidden;
}
.header-gen h1 a {
  font-size: clamp(1.5rem, 1.2rem + 1vi, 2rem);
  text-align: left;
}
.header-gen h2 {
  font-size: clamp(1rem, 0.7rem + 1vi, 1.5rem);
}
.category-wrapper {
  border: 2px solid black;
  overflow: hidden;
  line-height: 5vh;
  background-color: rgba(2, 5, 7, 0.8);
}
.categorias ul li a {
  background-size: cover;
  text-transform: uppercase;
  background-position: center;
  font-size: clamp(1rem, -0.2rem + 4vi, 3rem);
  font-weight: 700;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: rgb(130, 187, 135);
  border: 2px solid rgb(130, 187, 135);
  align-items: center;
  text-decoration: none;
  height: 20vh;
  display: block;
  text-align: center;
  padding-top: 9vh;
  margin: 10px;
}
.categorias ul li a:hover {
  filter: brightness(125%);
}
.categorias .mark {
  background-image: url(../img/mark.jpg);
}
.categorias .helly {
  background-image: url(../img/helly.jpg);
}
.categorias .dylan {
  background-image: url(../img/dylan.png);
}
.categorias .irving {
  background-image: url(../img/irving.jpg);
}
.text-category {
  text-align: justify;
  overflow-y: hidden;
  scrollbar-color: black rgb(130, 187, 135);
  overflow-x: hidden;
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
  text-align: center;
  align-items: center;
}
#text-placeholder {
  padding-top: 35vh;
  display: none;
}
#current {
  filter: brightness(125%);
  pointer-events: none;
  cursor: not-allowed;
}
article {
  text-align: left;
}
article h3 {
  text-align: left;
  padding-bottom: 0rem;
  padding-left: 1rem;
  text-decoration: underline;
}
article h2 {
  padding-top: 1rem;
  padding-bottom: 2rem;
  text-align: center;
}
article p {
  line-height: 2rem;
  padding: 1rem;
}
article a {
  color: inherit;
  text-decoration: none;
}
.trailer {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}
iframe {
  width: clamp(17.5rem, 7rem + 35vi, 35rem);
  height: clamp(9.84375rem, 3.9375rem + 19.6875vi, 19.6875rem);
}
.detalles {
  padding: 2rem;
  line-height: 2rem;
}
figure {
  text-align: center;
}
figure img {
  width: 75vw;
}
table {
  padding: clamp(0rem, -0.6rem + 2vi, 1rem);
  font-size: clamp(0.75rem, 0.6rem + 0.5vi, 1rem);
}
table * {
  padding-top: 2rem;
}
table thead th {
  border: 1px solid;
  text-align: center;
  padding-bottom: 1.5%;
  padding-top: 1.5%;
}
table tbody * {
  border: 1px solid;
  text-align: center;
  padding-bottom: 1.5%;
  padding-top: 1.5%;
}
.pictures {
  display: flex;
}
.pictures img {
  width: 50%;
}
@media screen and (min-width: 571px) {
  body {
    overflow-y: scroll;
  }
  .content-cover {
    height: 90vh;
    width: 90vw;
  }
  figure img {
    width: 70%;
  }
  .text-cover {
    overflow-y: scroll;
  }
  .text-category {
    overflow-y: scroll;
  }
}
@media screen and (min-width: 992px) {
  html {
    background-position: initial;
  }
  body {
    overflow-y: auto;
  }
  .content-cover {
    width: 40vw;
    height: 80vh;
  }
  .text-cover {
    overflow-y: scroll;
  }
  .container-gen {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .category-wrapper {
    display: flex;
    height: 85vh;
  }
  .categorias ul {
    justify-content: center;
    list-style-type: none;
  }
  .categorias ul li a {
    display: flex;
    width: 18vw;
    padding: 0;
    justify-content: center;
    font-size: 2vw;
  }
  #text-placeholder {
    display: block;
  }
  figure img {
    width: 60%;
  }
  .text-category {
    overflow-y: scroll;
  }
}

